import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import styled, { createGlobalStyle } from "styled-components";
import { isDarkAtom, toDoState } from "./atoms";
import Board from "./Components/Board";
import { ThemeProvider } from "styled-components";
import { darkTheme, lightTheme } from "./theme";
// global style
const GlobalStyle = createGlobalStyle`
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}
/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
    display: none;
}

html{
  width: 100wh;
  height: 100vh;
  font-family: 'Montserrat', sans-serif;
  background:${(props) => props.theme.bgColor} fixed;
  background-repeat: no-repeat;
  color:black;
}
body {
  line-height: 1;
  font-weight: 600;
}
menu, ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {

}

select{
  border-radius: 0;
}

input {
  appearance: none;
  border-radius: 0;
}

a {
  text-decoration: none;
  color:inherit;
}
`;

const ThemeContainer = styled.div`
  width: 100%;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  height: 4rem;
`;
const ThemeButton = styled.div`
  color: ${(props) => props.theme.textColor};
  position: fixed;
  font-size: 2rem;
  margin-right: 3rem;
  margin-top: 1rem;
  cursor: pointer;
`;

const Title = styled.div`
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 3rem;
  margin-bottom: 3rem;
  margin-top: 5rem;
  color: ${(props) => props.theme.titleColor};
  font-family: "Caveat", cursive;
  @media screen and (max-width: 1090px) {
    margin-top: 0;
    margin-bottom: 1.5rem;
  }
`;

const Wrapper = styled.div`
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  max-width: 680px;
`;

//   
const Boards = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  gap: 10px;

  align-items: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: center;

  @media screen and (max-width: 1090px) {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 5rem;
  }
`;

function App() {
  // color theme
  const isDark = useRecoilValue(isDarkAtom);
  const setDarkAtom = useSetRecoilState(isDarkAtom);
  const toggleDarkAtom = () => {
    setDarkAtom((prev) => !prev);
  };

  const [toDos, setToDos] = useRecoilState(toDoState);
  // onDragEnd :     
  // destination :     
  // source :    -    index, droppableId 
  const onDragEnd = (info: DropResult) => {
    const { destination, draggableId, source } = info;
    // destination     
    if (!destination) return;
    //      
    if (destination?.droppableId === source.droppableId) {
      setToDos((allBoards) => {
        // source droppableId array  
        const boardCopy = [...allBoards[source.droppableId]]; // toDo or Doing or Done array .
        const taskObj = boardCopy[source.index]; // toDo object 
        // 1. source.index  .
        boardCopy.splice(source.index, 1); // source.index   1 
        // 2. item  destination.index ,    item .
        // (item draggabledId .)
        // ( destination   .     )
        boardCopy.splice(destination?.index, 0, taskObj); // toDo object (taskObj)  .
        // boardCopy,  State,  Boards   
        // oldToDos object .
        // oldToDos  ,     
        return {
          ...allBoards, //   board 
          [source.droppableId]: boardCopy, //   board. ()
        };
      });
    }
    //       
    if (destination.droppableId !== source.droppableId) {
      setToDos((allBoards) => {
        // 1. Source board  . - 
        // (    source.droppableId )
        const sourceBoard = [...allBoards[source.droppableId]];
        const taskObj = sourceBoard[source.index];
        // 2. destinationBoard  -  
        const destinationBoard = [...allBoards[destination.droppableId]];
        // 3. sourceboard 
        sourceBoard.splice(source.index, 1);
        // 4.  destination board 
        // (draggableId   board index )
        destinationBoard.splice(destination?.index, 0, taskObj);
        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard,
        };
      });
    }


  };

  return (
    <ThemeProvider theme={isDark ? darkTheme : lightTheme}>
      <GlobalStyle />
      <DragDropContext onDragEnd={onDragEnd}>
        {/*    */}
        <ThemeContainer>
          <ThemeButton onClick={toggleDarkAtom}>
            {isDark ? "" : ""}
          </ThemeButton>
        </ThemeContainer>
        {/*   */}
        <Title>Trello</Title>
        <Wrapper>
          {/* Object.keys(toDos)   board  Id . */}
          {/*   boardId map   board . */}
          <Boards>
            {Object.keys(toDos).map((boardId) => (
              <Board key={boardId} toDos={toDos[boardId]} boardId={boardId} />
            ))}
          </Boards>
        </Wrapper>
      </DragDropContext>
    </ThemeProvider>
  );
}

export default App;
